.about {
	padding: 100px 0;
	background: $gray_light;
	.container {
		max-width: 1560px;
		@include flex(row, between, center, wrap); }
	.inner {
		position: relative; }
	.bowl {
		display: block;
		max-width: 770px;
		width: 100%; }
	.business-card {
		position: absolute;
		z-index: 2;
		left: 0;
		bottom: 0;
		transform: translate(0, 10%); }
	.icons {
		position: absolute;
		z-index: 2;
		right: 0;
		top: 0;
		transform: translate(23%, -6%); }
	&__title {
		font-size: 35px;
		font-weight: 700;
		position: absolute;
		left: 0;
		top: 0;
		b {
			display: block;
			font-size: 42px;
			font-weight: 800;
			line-height: 1.5; } }
	.list {
		margin: 0 auto 0 170px;
		font-size: 18px;
		font-weight: 500;
		line-height: 1.45;
		.line {
			border-top: 4px solid rgba(230, 47, 104, 0.7);
			border-right: 4px solid rgba(230, 47, 104, 0.7);
			position: absolute;
			right: calc( 100% + 19px );
			bottom: 80%; }
		li {
			position: relative;
			&:before {
				content: '';
				display: block;
				position: absolute;
				width: 14px;
				height: 14px;
				background: $pink;
				border-radius: 100%;
				left: -28px;
				top: 4px; }
			&:after {
				content: '';
				display: block;
				position: absolute;
				width: 18px;
				height: 18px;
				background: transparent;
				border-radius: 100%;
				border: 1px solid $pink;
				left: -31px;
				top: 1px; }
			&:not(:last-child) {
				margin: 0 0 57px 0; }
			&:nth-child(1) {
				.line {
					width: 295px;
					height: 45px; } }
			&:nth-child(2) {
				.line {
					width: 140px;
					height: 35px; } }
			&:nth-child(3) {
				.line {
					width: 87px;
					height: 4px;
					bottom: 50%; } }
			&:nth-child(4) {
				.line {
					width: 87px;
					height: 4px;
					bottom: 50%; } }
			&:nth-child(5) {
				.line {
					width: 142px;
					height: 48px;
					bottom: auto;
					top: 15px;
					border-top: none;
					border-bottom: 4px solid rgba(230, 47, 104, 0.7); } }
			&:nth-child(6) {
				.line {
					bottom: auto;
					top: 15px;
					width: 282px;
					height: 48px;
					border-top: none;
					border-bottom: 4px solid rgba(230, 47, 104, 0.7); } } } } }

